@use './reset' as reset;

* {
  font-family: 'Lato', sans-serif;
}
.header-container {
  height: 25vh;
  background-image: url('http://source.unsplash.com/1920x1080/?landscape');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

.form-section {
  .form-container {
    display: flex;
    flex-wrap: wrap;
    margin: 1rem;
    gap: 0.5rem;

    input {
      width: 100%;
      padding: 0.5rem;
      background-color: #fff;
      border: 1px solid black;
      margin-block: 0.3rem;
    }

    .codeMsg {
      width: 100%;
      background-color: lighten($color: rgb(61, 245, 190), $amount: 10%);
      text-decoration: none;
      text-transform: uppercase;
      color: #000;
      padding: 0.5rem 1rem;
      letter-spacing: 3px;
      border-radius: 5px;
      margin-bottom: 0.3rem;

      i {
        margin-right: 0.5rem;
      }
    }

    .codeMsg:hover {
      background-color: darken($color: rgb(61, 245, 190), $amount: 10%);
      transition: all ease-in 0.2s;
    }

    .searchBtn {
      display: inline-block;
      background-color: #fff;
      border: solid 1px black;
      text-transform: uppercase;
      font-weight: bold;
      letter-spacing: 3px;
      font-size: 1.2rem;
      width: 100%;
      padding: 0.5rem 1rem;
      outline: none;
      cursor: pointer;
    }

    .searchBtn:hover {
      background-color: black;
      color: #fff;
      transition: all ease-in 0.2s;
    }
  }
  .msgContainer {
    margin-inline: 1rem;
    .successMsg {
      width: 100%;
      background-color: lighten($color: rgb(156, 255, 91), $amount: 10%);
      text-decoration: none;
      text-transform: uppercase;
      color: #000;
      padding: 0.5rem 1rem;
      letter-spacing: 3px;
      border-radius: 5px;
      margin-bottom: 0.3rem;
    }

    i {
      margin-right: 0.5rem;
    }

    .cityMsg,
    .countryMsg,
    .noDataMsg {
      width: 100%;
      background-color: lighten($color: rgb(255, 88, 88), $amount: 10%);
      text-decoration: none;
      text-transform: uppercase;
      color: #000;
      padding: 0.5rem 1rem;
      letter-spacing: 3px;
      border-radius: 5px;
      margin-bottom: 0.3rem;
    }

    i {
      margin-right: 0.5rem;
    }
  }
}

.weatherContainer {
  padding: 1rem;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  flex-direction: column;

  h1 {
    border-bottom: 5px solid #000;
    margin-bottom: 1rem;
    letter-spacing: 3px;
  }
  p {
    font-size: 1.2rem;
    padding-block: 0.3rem;
    letter-spacing: 3px;
  }

  p:first-of-type {
    text-transform: capitalize;
    font-weight: 900;
  }

  p:nth-of-type(2) {
    text-transform: capitalize;
  }
}

.footerContainer {
  background-color: #000;
  color: #fff;
  text-align: center;
  width: 100%;
  padding: 0.5rem 1rem;
  position: fixed;
  bottom: 0;
  p {
    letter-spacing: 3px;
  }
}

@media only screen and (min-width: 1920px) {
  .header-container {
    height: 40vh;
    background-image: url('http://source.unsplash.com/2560x1440/?landscape');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
  }
}
